.header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 10;
  display: flex;
  align-items: center;
  padding: var(--offset) var(--offset-s);
  transition-duration: 0.5s;
  transition-property: transform, background-color;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.nav__logo {
  display: flex;
  align-items: center;
  gap: calc(var(--offset) * 0.7);
  margin-right: 0.5em;
  transition-duration: 0.5s;
  transition-property: opacity;
}
.nav__logo:hover, .nav__logo:focus, .nav__logo:focus-within {
  opacity: 0.6;
}
.nav__logo svg:nth-of-type(1) {
  fill: var(--color-text-secondary);
  width: calc(var(--offset) * 2.5);
  height: calc(var(--offset) * 2.5);
}
.nav__logo svg:nth-of-type(2) {
  fill: var(--color-text-primary);
  height: var(--offset);
  width: calc(var(--offset) * 9);
}
.nav__list {
  display: flex;
  align-items: center;
  gap: calc(var(--offset) * 2);
  justify-content: space-between;
  margin: 0 auto;
}
.nav-lang {
  position: relative;
  margin-left: calc(var(--offset) * 0.5);
  font-weight: 600;
  font-size: var(--fz);
  letter-spacing: -0.04em;
  color: var(--color-text-primary);
  border-radius: 50%;
  box-shadow: 0 0 0 0.05em var(--color-text-primary);
  transition-duration: 0.5s;
  transition-property: color, box-shadow;
}
.nav-lang:hover, .nav-lang:focus {
  color: var(--color-text-secondary);
  box-shadow: 0 0 0 0.05em var(--color-text-secondary);
}
.nav__register {
  margin-left: calc(var(--offset) * 0.5);
  background-color: var(--color-text-secondary);
  border-radius: 2em;
}
.nav__register:hover, .nav__register:focus {
  color: var(--color-text-secondary);
  background-color: var(--color-bg-secondary);
}
.nav__login {
  margin-left: calc(var(--offset) * 1);
}
.nav__member {
  margin-left: 0.5em;
  display: flex;
  align-items: center;
  gap: 0.5em;
  padding: var(--offset) calc(var(--offset) * 0.8) !important;
  border-radius: 2em;
  font-style: italic;
  font-weight: 600;
  font-size: calc(var(--fz) * 0.8);
  letter-spacing: -0.04em;
  background: linear-gradient(130deg, #e44134 0%, #fff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  position: relative;
  z-index: 1;
  overflow: hidden;
  transition: opacity 0.5s;
  min-width: fit-content;
}
.nav__member svg {
  width: calc(var(--offset) * 1.2);
  height: calc(var(--offset) * 0.8);
  fill: #EF928B;
}
.nav__member:before {
  content: "";
  display: block;
  position: absolute;
  z-index: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.12);
  transition: opacity 0.5s;
}
.nav__member:hover, .nav__member:focus, .nav__member:focus-within {
  opacity: 0.8;
}
.nav__member:hover::before, .nav__member:focus::before, .nav__member:focus-within::before {
  opacity: 0.8;
}
.nav__link {
  font-weight: 400;
  font-size: var(--fz);
  letter-spacing: -0.03em;
  color: var(--color-text-primary);
  transition-duration: 0.5s;
  transition-property: background-color, color;
  padding: calc(var(--offset) * 0.3) 0;
  min-width: fit-content;
  white-space: nowrap;
}
.nav__link.current {
  border-bottom: 1px solid var(--color-text-primary);
}
.nav__link:hover, .nav__link:focus {
  color: var(--color-text-secondary);
}

.btn-white {
  padding: calc(var(--offset) * 0.8);
}

.dropdown {
  display: flex;
  flex-direction: column;
  gap: 0.5em;
  position: absolute;
  z-index: 10;
  bottom: -170%;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 1em;
  padding: calc(var(--offset) * 0.8);
  box-shadow: 0 0 0 0.05em var(--color-text-primary);
}
.dropdown-item {
  font-weight: 600;
  font-size: var(--fz);
  letter-spacing: -0.04em;
  color: var(--color-text-primary);
}
.dropdown-link {
  transition-duration: 0.5s;
  transition-property: color;
}
.dropdown-link:hover, .dropdown-link:focus, .dropdown-link:focus-within {
  color: var(--color-text-secondary);
}

.profile {
  display: flex;
  gap: var(--offset);
  align-items: center;
  margin: 0 calc(var(--offset) * 2);
  transition-duration: 0.5s;
  transition-property: opacity;
}
.profile:hover, .profile:focus, .profile:focus-within {
  opacity: 0.7;
}
.profile img {
  width: calc(var(--offset) * 2.5);
  height: calc(var(--offset) * 2.5);
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 0 0.05em var(--color-text-primary);
  object-fit: cover;
}
.profile__info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 0.2em;
  padding: 0.5em 0;
}
.profile__info b {
  font-weight: 500;
  font-size: var(--fz);
  letter-spacing: -0.05em;
}
.profile__info span {
  font-weight: 400;
  font-size: var(--fz);
  color: #787b83;
}

.header-hide {
  transform: translateY(-100%);
}

.scroll-header {
  background-color: var(--color-bg-secondary);
}

.footer {
  background-color: var(--color-bg-primary);
  border-top-left-radius: var(--offset);
  border-top-right-radius: var(--offset);
  overflow: hidden;
  padding: var(--offset-s);
  display: flex;
  align-items: flex-start;
  gap: var(--offset);
}
.footer-contacts {
  display: flex;
  flex-direction: column;
  gap: var(--offset);
  margin-left: auto;
}
.footer-contacts b {
  font-weight: 500;
  font-size: calc(var(--fz) * 1.6);
  line-height: 119%;
  letter-spacing: -0.03em;
  color: var(--color-text-primary);
}
.footer-contacts a {
  max-width: 80%;
  font-weight: 600;
  font-size: calc(var(--fz) * 1.2);
  line-height: 119%;
  letter-spacing: -0.02em;
  color: var(--color-text-primary);
}
.footer-links {
  display: flex;
  flex-direction: column;
  gap: calc(var(--offset) * 0.8);
}
.footer-links li a {
  font-weight: 600;
  font-size: var(--fz);
  line-height: 139%;
  letter-spacing: -0.03em;
  color: var(--color-text-primary);
}
.footer-icons {
  display: flex;
  align-items: center;
  gap: calc(var(--offset) * 2);
  margin-left: auto;
}
.footer-social {
  display: flex;
  align-items: center;
  gap: calc(var(--offset) * 2);
}
.footer-social li a svg {
  width: calc(var(--offset) * 1.2);
  height: calc(var(--offset) * 1.2);
  fill: #787B83;
  transition-duration: 0.5s;
  transition-property: fill;
}
.footer-social li:hover svg, .footer-social li:focus-within svg, .footer-social li:focus svg {
  fill: var(--color-text-secondary);
}

.footer-location {
  max-width: 80%;
  font-weight: 400 !important;
  font-size: calc(var(--fz) * 0.75) !important;
  line-height: 119%;
  letter-spacing: -0.02em;
  color: var(--color-text-primary);
}

.footer-auth {
  display: flex;
  align-items: center;
  gap: calc(var(--offset) * 0.7);
}

.footer-auth__btn {
  border-radius: 2em;
  padding: calc(var(--offset) * 0.6) calc(var(--offset) * 1.2);
  color: #fff;
  transition-property: background-color, box-shadow;
  transition-duration: 0.5s;
}

.login {
  font-weight: 400;
  font-size: calc(var(--fz) * 0.9);
  letter-spacing: -0.04em;
  box-shadow: 0 0 0 0.05em var(--color-text-primary);
}
.login:hover, .login:focus, .login:focus-within {
  background-color: #fc4739;
  box-shadow: 0 0 0 0.05em #fc4739;
}

.register {
  font-weight: 700;
  background-color: #fc4739;
  box-shadow: 0 0 0 0.05em #fc4739;
}
.register:hover, .register:focus, .register:focus-within {
  background-color: inherit;
  box-shadow: 0 0 0 0.05em var(--color-text-primary);
}

.footer a {
  transition-duration: 0.5s;
  transition-property: color;
}
.footer a:hover, .footer a:focus, .footer a:focus-within {
  color: #fc4739;
}

.main-head {
  width: 100%;
  min-height: 100vh;
  position: relative;
  padding: var(--offset-s);
  padding-top: var(--offset-xl);
  display: flex;
  flex-direction: column;
}
.main-head__img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.main-head__img img {
  width: 100%;
  height: 100%;
  z-index: 0;
}
.main-head__desc {
  margin-top: auto;
  position: relative;
  z-index: 1;
  margin-right: auto;
  max-width: 50%;
}
.main-head__desc h1 {
  font-weight: 600;
  font-size: calc(var(--fz) * 3);
  line-height: 1.2;
  letter-spacing: -0.06em;
  color: var(--color-text-primary);
  margin-bottom: calc(var(--offset) * 1.5);
}
.main-head__desc h1 strong {
  color: #fc4739;
}
.main-head__desc p {
  font-weight: 500;
  font-size: var(--fz);
  line-height: 128%;
  letter-spacing: -0.06em;
  color: var(--color-text-primary);
  width: 80%;
  margin-bottom: calc(var(--offset) * 1.8);
}
.main-head__link {
  padding: calc(var(--offset) * 0.3) calc(var(--offset) * 0.3) calc(var(--offset) * 0.3) calc(var(--offset) * 1.2);
  width: fit-content;
  box-shadow: 0 0 0 0.05em var(--color-text-primary);
  border-radius: 3em;
  display: flex;
  align-items: center;
  gap: 1.5em;
  transition-duration: 0.5s;
  transition-property: box-shadow;
}
.main-head__link:hover, .main-head__link:focus, .main-head__link:focus-within {
  box-shadow: 0 0 0 0.05em var(--color-text-secondary);
}
.main-head__link:hover b, .main-head__link:focus b, .main-head__link:focus-within b {
  color: var(--color-text-secondary);
}
.main-head__link:hover span, .main-head__link:focus span, .main-head__link:focus-within span {
  background-color: var(--color-text-secondary);
}
.main-head__link:hover span svg, .main-head__link:focus span svg, .main-head__link:focus-within span svg {
  stroke: var(--color-text-primary);
}
.main-head__link b {
  font-weight: 600;
  font-size: calc(var(--fz) * 0.9);
  line-height: 1;
  letter-spacing: -0.04em;
  color: var(--color-text-primary);
  transition-duration: 0.5s;
  transition-property: color;
}
.main-head__link span {
  background-color: var(--color-text-primary);
  border-radius: 50%;
  width: calc(var(--offset) * 2);
  height: calc(var(--offset) * 2);
  display: flex;
  align-items: center;
  justify-content: center;
  transition-duration: 0.5s;
  transition-property: background-color;
}
.main-head__link span svg {
  width: calc(var(--offset) * 0.8);
  height: calc(var(--offset) * 0.9);
  stroke: var(--color-icon);
  transition-duration: 0.5s;
  transition-property: stroke;
}
.main-head__logo {
  position: relative;
  z-index: 1;
  margin-left: auto;
  display: flex;
  align-items: center;
  gap: calc(var(--offset) * 2);
}
.main-head__logo svg:nth-of-type(1) {
  width: calc(var(--offset) * 6);
  height: calc(var(--offset) * 6.5);
}
.main-head__logo svg:nth-of-type(2) {
  width: calc(var(--offset) * 20);
  height: calc(var(--offset) * 2);
}

.directions {
  padding: var(--offset-s);
  min-height: 100vh;
}
.directions-head {
  display: flex;
  align-items: flex-start;
  gap: var(--offset-s);
  justify-content: space-between;
  margin-bottom: calc(var(--offset) * 2);
}
.directions-head p {
  font-weight: 500;
  font-size: var(--fz);
  line-height: 128%;
  letter-spacing: -0.06em;
  color: var(--color-text-primary);
}
.directions-cards {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  column-gap: calc(var(--offset) * 2);
}
.directions-card {
  padding: calc(var(--offset) * 2) calc(var(--offset) * 2.5);
  display: flex;
  flex-direction: column;
  gap: 1em;
  align-items: center;
  transition-duration: 0.5s;
  transition-property: background-color;
  flex: 1;
}
.directions-card__image {
  width: calc(var(--offset) * 4);
  height: calc(var(--offset) * 4);
  border-radius: 50%;
  overflow: hidden;
}
.directions-card__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition-duration: 0.5s;
  transition-property: transform;
}
.directions-card b {
  font-weight: 500;
  font-size: calc(var(--fz) * 1.2);
  line-height: 96%;
  letter-spacing: -0.06em;
  text-align: center;
  color: var(--color-text-primary);
}
.directions-card:hover, .directions-card:focus, .directions-card:focus-within {
  background-color: var(--color-bg-secondary);
}
.directions-card:hover img, .directions-card:focus img, .directions-card:focus-within img {
  transform: scale(1.1);
}

.title {
  font-weight: 600;
  font-size: calc(var(--fz) * 2.5);
  line-height: 1.1;
  letter-spacing: -0.06em;
  color: var(--color-text-primary);
  width: 100%;
}
.title strong {
  color: var(--color-text-secondary);
}

.advantages {
  padding: var(--offset-s);
  min-height: 80vh;
}
.advantages h3 {
  margin-bottom: calc(var(--offset) * 2);
}
.advantages-cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: var(--offset);
  margin-bottom: var(--offset-xl);
}
.advantages-card {
  background-color: var(--color-bg-secondary);
  padding: calc(var(--offset) * 2);
  border-radius: 0.5em;
  overflow: hidden;
}
.advantages-card__head {
  display: flex;
  align-items: center;
  gap: calc(var(--offset) * 2);
  margin-bottom: calc(var(--offset) * 1.5);
  padding-top: var(--offset);
  border-top: 0.05em solid #979eb2;
}
.advantages-card__head svg {
  width: calc(var(--offset) * 2);
  height: calc(var(--offset) * 2.5);
}
.advantages-card__head b {
  font-weight: 600;
  font-size: calc(var(--fz) * 1.5);
  line-height: 1.2;
  letter-spacing: -0.06em;
  color: var(--color-text-primary);
}
.advantages-card ul {
  margin-left: calc(var(--offset) * 4);
  display: flex;
  flex-direction: column;
  gap: calc(var(--offset) * 0.5);
}
.advantages-card ul li {
  display: grid;
  grid-template-columns: 5% 95%;
  gap: var(--offset);
}
.advantages-card ul li svg {
  width: var(--offset);
  height: var(--offset);
}
.advantages-card ul li p {
  font-weight: 500;
  font-size: var(fz);
  line-height: 124%;
  color: #979eb2;
}

.advantages-info {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  gap: var(--offset-s);
  margin-bottom: var(--offset-s);
}
.advantages-col {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.advantages-col h3 {
  font-weight: 500;
  font-size: calc(var(--fz) * 2.2);
  line-height: 1.1;
  letter-spacing: -0.06em;
  color: var(--color-text-primary);
}
.advantages-col h3 b {
  color: var(--color-text-secondary);
}
.advantages-col p {
  margin-bottom: calc(var(--offset) * 3);
}
.advantages-col a {
  margin-bottom: calc(var(--offset) * 4);
}

.advantages-info__desc {
  display: grid;
  grid-template-columns: 5% 95%;
  gap: var(--offset);
  padding: calc(var(--offset) * 1.5) 0;
  border-bottom: 0.05em solid #979eb2;
}
.advantages-info__desc:nth-of-type(1) {
  border-top: 0.05em solid #979eb2;
}
.advantages-info__desc svg {
  width: calc(var(--offset) * 1.5);
}
.advantages-info__desc p {
  margin: 0;
}

.advantages-info__col {
  display: flex;
  flex-direction: column;
  gap: 0.5em;
}
.advantages-info__col b {
  font-weight: 500;
  font-size: calc(var(--fz) * 1.1);
  line-height: 124%;
  color: var(--color-text-primary);
}
.advantages-info__col p {
  font-weight: 500;
  font-size: var(--fz);
  line-height: 124%;
  color: #979eb2;
}

.news {
  padding: var(--offset-s);
  background-color: var(--color-text-primary);
  border-top-left-radius: 1em;
  border-top-right-radius: 1em;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
}
.news-slider {
  overflow-x: hidden;
  border-top-left-radius: 1em;
  border-top-right-radius: 1em;
  height: auto;
  flex: 1;
}
.news-slide {
  padding-right: var(--offset);
  flex: 0 0 33.5%;
}
.news-slide b {
  font-weight: 600;
  font-size: calc(var(--fz) * 1.1);
  line-height: 1.2;
  color: var(--color-default-black);
  -webkit-line-clamp: 3;
  line-clamp: 3;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.news-slide p {
  font-weight: 500;
  font-size: calc(var(--fz) * 0.8);
  line-height: 1.2;
  color: #787b83;
  margin-top: calc(var(--offset) * 0.8);
  margin-bottom: calc(var(--offset) * 1.5);
  -webkit-line-clamp: 2;
  line-clamp: 2;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.news h3 {
  font-weight: 500;
  font-size: calc(var(--fz) * 2.5);
  line-height: 1.2;
  letter-spacing: -0.06em;
  color: var(--color-bg-primary);
  margin-bottom: calc(var(--offset) * 2);
}
.news-container {
  display: flex;
  align-items: stretch;
  width: 100%;
}
.news-desc {
  padding: 0 calc(--offset * 0.5);
}
.news-image {
  height: auto;
  border-radius: 1em;
  overflow: hidden;
  width: 100%;
  height: 60%;
  margin-bottom: calc(var(--offset) * 1.5);
}
.news-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  aspect-ratio: 16/9;
  transition-duration: 1s;
  transition-property: transform;
}

.news-date {
  display: grid;
  grid-template-columns: 5% 95%;
  align-items: center;
  gap: calc(var(--offset) * 0.3);
}
.news-date svg {
  width: var(--offset);
  height: var(--offset);
}
.news-date span {
  font-weight: 400;
  font-size: calc(var(--fz) * 0.7);
  line-height: 1.2;
  color: #787b83;
}

.news-slide:hover .news-image img,
.news-slide:focus-within .news-image img,
.news-slide:focus .news-image img {
  transform: scale(1.1);
}

.reviews {
  background-color: var(--color-text-primary);
  min-height: 90vh;
  padding: var(--offset-s);
}
.reviews-head {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  gap: var(--offset);
  margin-bottom: calc(var(--offset) * 2);
}
.reviews-head h3 {
  color: var(--color-bg-primary);
}
.reviews__buttons {
  display: flex;
  align-items: center;
  gap: calc(var(--offset) * 0.6);
}
.reviews__btn {
  cursor: pointer;
  width: calc(var(--offset) * 2.2);
  height: calc(var(--offset) * 2.2);
  border-radius: 50%;
  box-shadow: 0 0 0 0.05em rgba(11, 17, 23, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  transition-duration: 0.5s;
  transition-property: background-color;
}
.reviews__btn:hover, .reviews__btn:focus, .reviews__btn:focus-within {
  background-color: var(--color-text-secondary);
  box-shadow: none;
}
.reviews__btn:hover svg, .reviews__btn:focus svg, .reviews__btn:focus-within svg {
  stroke: #0B1117;
  transition-duration: 0.5s;
  transition-property: stroke;
}
.reviews__btn svg {
  width: calc(var(--offset) * 0.7);
  height: calc(var(--offset) * 0.6);
  stroke: rgba(11, 17, 23, 0.5);
}
.reviews-start {
  display: flex;
  align-items: stretch;
  gap: calc(var(--offset) * 3);
  margin-bottom: var(--offset-s);
}
.reviews-start h3 {
  font-weight: 500;
  font-size: calc(var(--fz) * 3);
  line-height: 103%;
  letter-spacing: -0.06em;
  color: #0b1117;
  max-width: 45%;
}
.reviews-col {
  display: flex;
  flex-direction: column;
  gap: calc(var(--offset) * 1);
}
.reviews-col p {
  font-weight: 500;
  font-size: calc(var(--fz) * 1.1);
  line-height: 1.2;
  letter-spacing: -0.05em;
  color: #353b3d;
  margin: 0;
}
.reviews-col p b {
  color: #c1c1c1;
  font-weight: 500;
}
.reviews-col__links {
  display: flex;
  align-items: center;
  gap: var(--offset);
  margin-top: calc(var(--offset) * 0.5);
}

.reviews__slider-container {
  display: flex;
}

.reviews__slide {
  padding: calc(var(--offset) * 1.5);
  background-color: #f5f5f5;
  margin-right: var(--offset);
  flex: 0 0 32%;
}
.reviews__slide p {
  font-weight: 500;
  font-size: calc(var(--offset) * 0.8);
  line-height: 1.2;
  letter-spacing: -0.05em;
  color: #353b3d;
}

.reviews__slide-head {
  display: flex;
  align-items: center;
  gap: var(--offset);
  margin-bottom: calc(var(--offset) * 1.5);
}

.reviews__slide-image {
  width: var(--offset-s);
  height: var(--offset-s);
  border-radius: 50%;
  overflow: hidden;
}
.reviews__slide-image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.reviews__slide-name {
  display: flex;
  flex-direction: column;
  gap: calc(var(--offset) * 0.5);
}
.reviews__slide-name b {
  font-weight: 600;
  font-size: var(--fz);
  letter-spacing: -0.05em;
  color: #353b3d;
}

.reviews-location {
  display: flex;
  align-items: center;
  gap: calc(var(--offset) * 0.4);
  font-weight: 400;
  font-size: calc(var(--fz) * 0.8);
  line-height: 100%;
  letter-spacing: -0.05em;
  color: #787b83;
}
.reviews-location svg {
  width: calc(var(--offset) * 0.5);
  height: calc(var(--offset) * 0.7);
}

.reviews__slider {
  overflow-x: hidden;
  width: 101.5%;
  margin-bottom: var(--offset-s);
}

.reviews-logout {
  padding: calc(var(--offset) * 0.6) calc(var(--offset) * 1.4);
  border-radius: 2em;
  box-shadow: 0 0 0 0.05em #141f2a;
  font-weight: 600;
  font-size: calc(var(--fz) * 0.8);
  letter-spacing: -0.04em;
  color: #141f2a;
  transition-duration: 0.5s;
  transition-property: background-color, box-shadow, color;
}
.reviews-logout:hover, .reviews-logout:focus, .reviews-logout:focus-within {
  background-color: var(--color-text-secondary);
  box-shadow: 0 0 0 0.05em var(--color-text-secondary);
  color: var(--color-text-primary);
}

.reviews-register {
  font-weight: 700;
  font-size: calc(var(--fz) * 0.9);
  letter-spacing: -0.02em;
  color: var(--color-text-primary);
  border-radius: 2em;
  padding: calc(var(--offset) * 0.6) calc(var(--offset) * 1.4);
  background-color: var(--color-text-secondary);
  transition-duration: 0.5s;
  transition-property: background-color, box-shadow, color;
}
.reviews-register:hover, .reviews-register:focus, .reviews-register:focus-within {
  background-color: inherit;
  box-shadow: 0 0 0 0.05em #141f2a;
  color: #141f2a;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}

body {
  background-color: var(--color-bg-primary);
  color: var(--color-text-primary);
  font-family: var(--ffI), sans-serif;
  touch-action: manipulation;
  user-select: auto;
  font-size: var(--fz);
  position: relative;
  min-height: 100vh;
}

:root {
  /* colors */
  --color-default-black: #000;
  --color-bg-primary: #0B1117;
  --color-bg-secondary: #141F2A;
  --color-icon: #353B3D;
  --color-text-primary: #fff;
  --color-text-secondary: #FC4739;
  --color-text-black: #000;
  --color-text-dark: #353B3D;
  --color-text-gray: #979EB2;
  --ffI: "Inter";
  --fz: max(calc(1 * 1vw), 15px);
  --offset: max(calc(1.25 * 1vw), 15px);
  --offset-xxxl: calc(var(--offset) * 10);
  --offset-xxl: calc(var(--offset) * 8);
  --offset-xl: calc(var(--offset) * 7);
  --offset-l: calc(var(--offset) * 5.5);
  --offset-m: calc(var(--offset) * 5);
  --offset-s: calc(var(--offset) * 4);
}

* {
  box-sizing: border-box;
}
*:focus {
  outline: none;
}
* *::before,
* *::after {
  box-sizing: inherit;
}

::selection {
  background-color: #d3d9e2;
  color: #45478d;
  -webkit-text-fill-color: #45478d;
}

::-webkit-scrollbar {
  width: 0 !important;
  height: 0 !important;
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  border-radius: 50px;
}

.container {
  padding: 0 var(--offset);
  max-width: 780px;
  margin: 0 auto;
  width: 100%;
  overflow: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  user-select: text;
  word-break: break-word;
}

p {
  user-select: text;
  word-break: break-word;
}

details {
  display: block;
}

summary {
  list-style: none;
  cursor: pointer;
}

summary::-webkit-details-marker {
  display: none;
}

a {
  text-decoration: none;
  color: inherit;
  outline: none;
  text-wrap: balance;
  user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -webkit-tap-highlight-color: transparent;
  touch-action: manipulation;
  outline: none;
}

img {
  display: block;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: var(--fz);
  padding: 0;
  font-family: "Inter", sans-serif;
  user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -webkit-tap-highlight-color: transparent;
  touch-action: manipulation;
  outline: none;
}
button svg,
button span {
  pointer-events: none;
}

input {
  font-family: var(--ffP), sans-serif;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

textarea {
  resize: none;
}

html {
  scroll-behavior: smooth;
}

[hidden],
[type=hidden],
.visually-hidden,
.tns-visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-wrapper {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  min-height: 100%;
  width: 100vw;
  overflow-x: hidden;
}